      //倒计时修改
      //#region
      //自定义弹窗
      change() {
        console.log('that', this);
        this.dialogFormVisible = true;
      },
      //关闭弹窗
      closedeep() {
        let that = diyTimeOut;
        that.dialogFormVisible = false;
        pie.dialogPieVisible = false;
        that.form = {
          name: "",
          region: ""
        };
      },
      //保存修改
      suretime() {
        let that = diyTimeOut;
        that.count = that.form.region;
        localStorage.setItem("olddata", JSON.stringify(that.count));
        that.done = that.form.name;
        localStorage.setItem("olddone", JSON.stringify(that.done));
        that.countdown();
        that.dialogFormVisible = false;
      },
      //#endregion
      // run自定义倒计时
      //#region
      tow(n) {
        return n >= 0 && n < 10 ? "0" + n : "" + n;
      },
      getDate() {
        //获取上次本地记录中的日期对象
        let that = diyTimeOut;
        let olddata = localStorage.getItem("olddata");
        if (olddata) {
          that.count = JSON.parse(olddata);
        }
        let olddone = localStorage.getItem("olddone");
        if (olddone) {
          that.done = JSON.parse(olddone);
        }
        var oDate = new Date(); //获取日期对象

        var oldTime = oDate.getTime(); //现在距离1970年的毫秒数

        var newDate = new Date(that.count);
        var newTime = newDate.getTime(); //2019年距离1970年的毫秒数

        var second = Math.floor((newTime - oldTime) / 1000); //未来时间距离现在的秒数
        that.nowtime = second;
        var day = Math.floor(second / 86400); //整数部分代表的是天；一天有24*60*60=86400秒 ；

        second = second % 86400; //余数代表剩下的秒数；

        var hour = Math.floor(second / 3600); //整数部分代表小时；

        second %= 3600; //余数代表 剩下的秒数；

        var minute = Math.floor(second / 60);
        second %= 60;
        that.day = that.tow(day);
        that.hour = that.tow(hour);
        that.minute = that.tow(minute);
        that.second = that.tow(second);
      },
      countdown() {
        let that = diyTimeOut;
        var times = setInterval(() => {
          that.getDate(); //递减
          if (that.nowtime <= 0) {
            // <=0 变成获取按钮
            clearInterval(times);
          }
        }, 1000); //1000毫秒后执行
      }
      //#endregion
    });

