    let clock = reactive({
      //run24小时时钟
      //#region
      // draw functions :
      clear(ctx) {
        // clear canvas function
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      },
      drawScene(ctx, canvas, clockRadius, clockImage) {
        // main drawScene function
        clock.clear(ctx); // clear canvas

        // get current time
        let date = new Date();
        let hours = date.getHours();
        let minutes = date.getMinutes();
        let seconds = date.getSeconds();
        // hours = hours > 12 ? hours - 12 : hours;
        let hour = hours + minutes / 60;
        let minute = minutes + seconds / 60;

        // save current context
        ctx.save();

        // draw clock image (as background)
        ctx.drawImage(clockImage, 0, 0, 250, 250);
        ctx.translate(canvas.width / 2, canvas.height / 2);
        ctx.beginPath();

        // draw numbers
        ctx.font = "11px Arial";
        ctx.fillStyle = "#000";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        for (let n = 1; n <= 24; n++) {
          let theta = (n - 6) * (Math.PI * 2) / 24;
          let x = clockRadius * 0.7 * Math.cos(theta);
          let y = clockRadius * 0.7 * Math.sin(theta);
          ctx.fillText(n, x, y);
        }

        // draw hour
        ctx.save();
        let theta1 = (hour - 6) * 2 * Math.PI / 24;
        ctx.rotate(theta1);
        ctx.beginPath();
        ctx.moveTo(-15, -5);
        ctx.lineTo(-15, 5);
        ctx.lineTo(clockRadius * 0.5, 1);
        ctx.lineTo(clockRadius * 0.5, -1);
        ctx.fill();
        ctx.restore();

        // draw minute
        ctx.save();
        let theta2 = (minute - 15) * 2 * Math.PI / 60;
        ctx.rotate(theta2);
        ctx.beginPath();
        ctx.moveTo(-15, -4);
        ctx.lineTo(-15, 4);
        ctx.lineTo(clockRadius * 0.8, 1);
        ctx.lineTo(clockRadius * 0.8, -1);
        ctx.fill();
        ctx.restore();

        // draw second
        ctx.save();
        let theta3 = (seconds - 15) * 2 * Math.PI / 60;
        ctx.rotate(theta3);
        ctx.beginPath();
        ctx.moveTo(-15, -3);
        ctx.lineTo(-15, 3);
        ctx.lineTo(clockRadius * 0.9, 1);
        ctx.lineTo(clockRadius * 0.9, -1);
        ctx.fillStyle = "#0f0";
        ctx.fill();
        ctx.restore();
        ctx.restore();
      },
      initialization() {
        let canvas, ctx;
        let clockRadius = 125;
        let clockImage;
        canvas = document.getElementById("canvas");
        ctx = canvas.getContext("2d");

        // var width = canvas.width;
        // var height = canvas.height;

        clockImage = new Image();
        clockImage.src = "https://static.runoob.com/images/mix/125855_nnla_89964.png";
        let that = clock;
        // setInterval(that.drawScene(ctx,canvas,clockRadius,clockImage), 1000); // loop drawScene
        setInterval(() => {
          that.drawScene(ctx, canvas, clockRadius, clockImage); //递减
        }, 1000); // loop drawScene
      }
      //#endregion
    });

